import NProgress from "nprogress";
import TicketService from "@/services/TicketService";

export const namespaced = true;

export const state = {
  organisation: {}
};

export const mutations = {
  setOrganisation(state, payload) {
    state.organisation = payload;
  }
};

export const actions = {
  getOrganisation({ commit, dispatch }, orgName) {
    NProgress.start();
    commit("setLoading", true, { root: true });
    return TicketService.getOrganisation(orgName).then(res => {
      const organisation = res.data.organisation;
      commit("setOrganisation", organisation);
      NProgress.done();
      commit("setLoading", false, { root: true });
    });
  }
};

export const getters = {
  organisation(state) {
    return state.organisation;
  }
};
