export const namespaced = true;
const DEFAULT_LAYOUT = "default";

export const state = {
  currentLayout: DEFAULT_LAYOUT + "-layout",
  showBanner: true
};

export const mutations = {
  setLayout(state, layout) {
    state.currentLayout = layout + "-layout";
  },
  setBannerVisibility(state, visibility) {
    state.showBanner = visibility;
  }
};

export const actions = {
  setLayout({ commit }, layout) {
    commit("setLayout", layout);
  },
  setBannerVisibility({ commit, }, visibility) {
    commit("setBannerVisibility", visibility)
  }
};

export const getters = {
  layout(state) {
    return state.currentLayout;
  },
  showBanner(state) {
    return state.showBanner;
  }
};
