import NProgress from "nprogress";
import ContentService from "@/services/ContentService";

export const namespaced = true;

export const state = {
  content: []
};

export const mutations = {
  addContent(state, payload) {
    state.content.push(payload);
  }
};

export const actions = {
  getContent({ commit }, id) {
    NProgress.start();
    commit("setLoading", true, { root: true });
    return ContentService.getContent(id).then(res => {
      const content = res.data.content;
      commit("addContent", content);
      NProgress.done();
      commit("setLoading", false, { root: true });
    });
  }
};

export const getters = {
  allContent(state) {
    return state.content;
  },
  contentByField: state => field => {
    return state.content.find(content => content.field === field);
  },
  contentById: state => id => {
    return state.content.find(content => content.id === id);
  }
};
