import NProgress from "nprogress";
import TicketService from "@/services/TicketService";

export const namespaced = true;

export const state = {
  ticketOffer: {}
};

export const mutations = {
  setTicketOffer(state, payload) {
    state.ticketOffer = payload;
  }
};

export const actions = {
  getNewTicketOffer({ commit, dispatch }, eventSlug) {
    NProgress.start();
    commit("setLoading", true, { root: true });
    return TicketService.getNewTicketOffer(eventSlug).then(res => {
      const ticketOffer = res.data.ticketOffer;
      dispatch("layouts/setLayout", ticketOffer.layout, { root: true });
      commit("setTicketOffer", ticketOffer);
      NProgress.done();
      commit("setLoading", false, { root: true });
    });
  },

  getNewPartnerTicketOffer({ commit, dispatch, getters }, eventSlug) {
    NProgress.start();
    commit("setLoading", true, { root: true });
    return dispatch("getNewTicketOffer").then(() => {
      let ticketOffer = getters.ticketOffer
      ticektOffer.kind = "partner"
      commit("setTicketOffer", ticketOffer)
      NProgress.done();
      commit("setLoading", false, { root: true });
    })
  },

  async registerPartner({ commit, dispatch, getters, rootGetters }) {
    NProgress.start();
    commit("setLoading", true, { root: true });

    return new Promise(async (resolve, reject) => {
      let res = await TicketService.registerPartner(
        getters.ticketOffer.id,
        rootGetters["people/person"].id,
        rootGetters["events/event"].id,
        rootGetters["organisations/organisation"].id
      )
      await dispatch("getTicketOffer", res.data.ticketOfferId)
      resolve()
    })
  },

  getTicketOffer({ commit, dispatch }, id) {
    NProgress.start();
    commit("setLoading", true, { root: true });
    return TicketService.getTicketOfferNew(id).then(res => {
      const ticketOffer = res.data.ticketOffer;
      dispatch("layouts/setLayout", ticketOffer.layout, { root: true });
      commit("setTicketOffer", ticketOffer);
      NProgress.done();
      commit("setLoading", false, { root: true });
    });
  },

  rejectTicketOffer({ commit, dispatch }, id) {
    NProgress.start();
    commit("setLoading", true, { root: true });
    return TicketService.rejectTicketNew(id)
      .then(() => {
        NProgress.done();
        commit("setLoading", false, { root: true });
        dispatch("getTicketOffer", id);
      })
      .catch(e => {
        NProgress.done();
        commit("setLoading", false, { root: true });
        console.log("ERROR", e);
      });
  }
};

export const getters = {
  ticketOffer(state) {
    return state.ticketOffer;
  }
};
