import axios from "axios";

const baseURL = process.env.VUE_APP_API_URL;

const apiClient = axios.create({
  baseURL: baseURL,
  withCredentials: false,
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json"
  }
});

export default {
  getProfile(id) {
    return apiClient.get("/people/" + id);
  },
  getNewProfile() {
    return apiClient.get("/people/new");
  },
  createOrUpdateProfile(data) {
    // Uses case insensitive email for lookup
    return apiClient.post("/people", data);
  },
  updateProfile(id, data) {
    console.log('update profile')

    return apiClient.patch("/people/" + id, data);
  },

  getSurvey(personId, surveyId) {
    return apiClient.get(`/people/${personId}/surveys/${surveyId}`);
  },

  // To check which view should be showed
  calcNeedsDetails(person) {
    return (
      !person.lastName ||
      // !person.data.educationLevel ||
      !person.data.foundedYesNo
    );
  }
};

